import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {

      user: JSON.parse(localStorage.getItem("user")) ||{
          userId: 0,
          userName: '',
          userPassword: '',
          phone: ''
      }

    },
    //获取
    //store.getters.getUser
    getters: {

      getUser: state=>{



          if(state.user.userId == 0){
              return null
          }

          if(state.user.userName == '' || state.user.userName ==null){
              return null;
          }
          if(state.user.userPassword == '' || state.user.userPassword ==null){
            return null;
        }
          return state.user
      }

    },
    //设置
    //store.commit('setUser', user）
    mutations:{
        setUser(state,user){
            localStorage.setItem('user', JSON.stringify(user));
            state.user = user
        }
    }
  })